/**
* 双随机-结果公示组件
* @module 系统
* @desc 统计分析
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <!-- 图 -->
        <IEcharts :option="bar" class="echarts-css"/>
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                border
                size="mini"
        >
            <el-table-column align="center" label="检查单位" prop="checkUnit" show-overflow-tooltip/>
            <el-table-column align="center" label="总数" prop="countSum" show-overflow-tooltip/>
            <el-table-column align="center" label="合格数" prop="sumGood" show-overflow-tooltip/>
            <el-table-column align="center" label="不合格数" prop="sumNotGood" show-overflow-tooltip/>
            <el-table-column align="center" label="不合格率" prop="pass" show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.pass | pass}}</template>
            </el-table-column>
        </el-table>

        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getAccountList"
                class="page-css"
        />
    </div>
</template>

<script>
    import {apiCheckcheckResultSearch} from "../../../api/case";
    import LjPagination from "../../../components/LjPagination";
    import LjSearchFrom from "../../../components/LjSearchForm";
    import IEcharts from "vue-echarts-v3/src/full.js";

    let that;
    export default {
        name: "statisticsPage",
        data() {
            return {
                bar: {
                    color: ["#3398DB"],
                    title: {
                        text: "检查结果统计不合格率（百分比）"
                    },
                    legend: {
                        data: []
                    },
                    tooltip: {},
                    xAxis: {
                        data: [] //柱状名称
                    },
                    yAxis: {
                        max: 100
                    },
                    series: [
                        {
                            name: "不合格比重（%）",
                            type: "bar",
                            data: [] //柱状数据
                        }
                    ]
                },

                form: {
                    pageSize: 20,
                    pageNum: 1
                },
                total: 0,
                input: "",
                list: []
            };
        },
        components: {
            LjPagination,
            LjSearchFrom,
            IEcharts
        },

        methods: {
            //查询+搜索
            getAccountList() {
                apiCheckcheckResultSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    let nopassArr = [];
                    let omggavArr = [];
                    this.list.forEach(item => {
                        nopassArr.push(item.pass);
                        omggavArr.push(item.checkUnit);
                    });
                    this.bar.series[0].data = nopassArr;
                    this.bar.xAxis.data = omggavArr;
                });
            }
        },
        beforeCreate: function () {
            that = this;
        },
        filters: {
            pass(val) {
                return val + "%";
            }
        },

        created() {
            this.getAccountList();
        }
    };
</script>

<style scoped>
    .echarts-css {
        height: 600px;
    }

    .page-css {
        margin-top: 10px;
    }
</style>
